
'use client';

import { useEffect, useState, useTransition } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { getUserHeaderData } from '@/lib/actions';
import type { HeaderData } from '@/lib/schemas';
import { useToast } from '@/hooks/use-toast';
import { Loader2, PlusCircle, Search, Trash, FilePenLine } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import { ProfileForm } from './profile-form';


interface HeaderContentProps {
  refreshKey: number;
  initialFetchComplete: boolean;
  setInitialFetchComplete: (value: boolean) => void;
  headerProfiles: HeaderData[];
  setHeaderProfiles: (profiles: HeaderData[]) => void;
  selectedProfile: HeaderData | null;
  setSelectedProfile: (profile: HeaderData | null) => void;
}

export function HeaderContent({ 
  refreshKey, 
  initialFetchComplete, 
  setInitialFetchComplete,
  headerProfiles,
  setHeaderProfiles,
}: HeaderContentProps) {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [editingProfile, setEditingProfile] = useState<HeaderData | null>(null);

  const handleManualRefresh = () => {
    startTransition(async () => {
        const databaseId = localStorage.getItem('databaseId');
        if (databaseId) {
            const result = await getUserHeaderData(databaseId);
            if (result.success && result.data) {
                setHeaderProfiles(result.data as HeaderData[]);
                toast({
                    title: 'Refresh Successful',
                    description: 'The data has been updated.',
                });
            } else {
                toast({
                    title: 'Error',
                    description: result.error || 'Failed to fetch header data.',
                    variant: 'destructive',
                });
            }
        }
    });
  };

  useEffect(() => {
    const fetchData = () => {
        startTransition(async () => {
            const databaseId = localStorage.getItem('databaseId');
            if (databaseId) {
                const result = await getUserHeaderData(databaseId);
                if (result.success && result.data) {
                    setHeaderProfiles(result.data as HeaderData[]);
                } else {
                    toast({
                        title: 'Error',
                        description: result.error || 'Failed to fetch header data.',
                        variant: 'destructive',
                    });
                }
            }
            if (!initialFetchComplete) {
                setInitialFetchComplete(true);
            }
        });
    };

    if (!initialFetchComplete || (refreshKey > 0 && !isPending)) {
        fetchData();
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [refreshKey, initialFetchComplete]);

  const getDisplayName = (profile: HeaderData) => {
    if (profile.entityType === 'Individual') {
      return `${profile.firstName} ${profile.lastName}`;
    }
    return profile.companyName;
  }

  const handleAddNew = () => {
    setEditingProfile(null);
    setIsFormOpen(true);
  };

  const handleEdit = (profile: HeaderData) => {
    setEditingProfile(profile);
    setIsFormOpen(true);
  };


  return (
    <div className="p-6 h-full">
        <ProfileForm 
          isOpen={isFormOpen} 
          setIsOpen={setIsFormOpen} 
          profile={editingProfile}
          onSuccess={handleManualRefresh}
        />
        <h1 className="text-3xl font-bold mb-1">Tax Profiles</h1>
        <p className="text-muted-foreground mb-6">Manage tax profiles for your organization.</p>
        <Card>
            <CardHeader>
                <CardTitle>All Tax Profiles</CardTitle>
                <CardDescription>A list of all tax profiles in your organization.</CardDescription>
                <div className="flex items-center gap-4 pt-2">
                    <div className="relative flex-1">
                        <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                        <Input placeholder="Search by TIN or name..." className="pl-8" />
                    </div>
                    <Button onClick={handleAddNew}>
                        <PlusCircle className="mr-2" />
                        New Profile
                    </Button>
                </div>
            </CardHeader>
            <CardContent>
                <div className="border rounded-md">
                    <Table>
                        <TableHeader>
                            <TableRow>
                                <TableHead>TIN</TableHead>
                                <TableHead>Name</TableHead>
                                <TableHead>Classification</TableHead>
                                <TableHead>Actions</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {isPending && !initialFetchComplete ? (
                                <TableRow>
                                    <TableCell colSpan={4} className="h-24 text-center">
                                        <div className="flex justify-center items-center">
                                            <Loader2 className="h-6 w-6 animate-spin mr-2" />
                                            <span>Loading profiles...</span>
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ) : headerProfiles.length > 0 ? (
                                headerProfiles.map((profile) => (
                                    <TableRow key={profile.tpTIN}>
                                        <TableCell className="font-mono">{profile.tpTIN}</TableCell>
                                        <TableCell className="font-medium">{getDisplayName(profile)}</TableCell>
                                        <TableCell>
                                            <Badge variant={profile.entityType === 'Individual' ? 'secondary' : 'default'}>
                                                {profile.entityType}
                                            </Badge>
                                        </TableCell>
                                        <TableCell>
                                            <div className="flex items-center gap-2">
                                                <Button variant="ghost" size="icon" onClick={() => handleEdit(profile)}>
                                                    <FilePenLine className="h-4 w-4" />
                                                    <span className="sr-only">Edit</span>
                                                </Button>
                                                <Button variant="ghost" size="icon" className="text-destructive hover:text-destructive">
                                                    <Trash className="h-4 w-4" />
                                                    <span className="sr-only">Delete</span>
                                                </Button>
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ))
                            ) : (
                                <TableRow>
                                    <TableCell colSpan={4} className="h-24 text-center">
                                        No tax profiles found. Click "New Profile" to add one.
                                    </TableCell>
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </div>
            </CardContent>
        </Card>
    </div>
  );
}
