
'use client';

import { useTransition } from 'react';
import { useForm, type SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
  DialogClose
} from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Loader2 } from 'lucide-react';
import { addTaxProfile } from '@/lib/actions';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import type { HeaderData } from '@/lib/schemas';
import { HeaderDataSchema } from '@/lib/schemas';
import { RadioGroup, RadioGroupItem } from './ui/radio-group';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';

interface ProfileFormProps {
  isOpen: boolean;
  setIsOpen: (open: boolean) => void;
  profile: HeaderData | null;
  onSuccess: () => void;
}

const months = [
    "January", "February", "March", "April", "May", "June", 
    "July", "August", "September", "October", "November", "December"
];

export function ProfileForm({ isOpen, setIsOpen, profile, onSuccess }: ProfileFormProps) {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();

  const form = useForm<HeaderData>({
    resolver: zodResolver(HeaderDataSchema),
    defaultValues: profile || {
      tpTIN: '',
      branchCode: '00000',
      rdoCode: '',
      entityType: 'Individual',
      cycleType: 'Calendar',
      monthSelect: '',
      companyName: '',
      lastName: '',
      firstName: '',
      middleName: '',
      tradeName: '',
      subStreet: '',
      street: '',
      barangay: '',
      cityMunicipality: '',
      province: '',
      zipCode: '',
    },
  });

  const entityType = form.watch('entityType');

  const onSubmit: SubmitHandler<HeaderData> = (data) => {
    startTransition(async () => {
        const databaseId = localStorage.getItem('databaseId');
        if (!databaseId) {
            toast({ title: 'Error', description: 'User session not found. Please log in again.', variant: 'destructive' });
            return;
        }

        const result = await addTaxProfile(data, databaseId);
        
        if (result.success) {
            toast({
                title: 'Success!',
                description: `Profile for ${data.entityType === 'Individual' ? `${data.firstName} ${data.lastName}` : data.companyName} has been saved.`,
            });
            onSuccess();
            setIsOpen(false);
            form.reset();
        } else {
            toast({
                title: 'Operation Failed',
                description: result.error,
                variant: 'destructive',
            });
        }
    });
  };
  
  const inputStyles = "bg-slate-100";

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogContent className="sm:max-w-[800px] bg-white text-black">
        <DialogHeader>
          <DialogTitle className="text-black">{profile ? 'Edit' : 'Create'} Tax Profile</DialogTitle>
          <DialogDescription className="text-slate-600">
            Fill in the details below to {profile ? 'update the' : 'create a new'} tax profile. Click save when you're done.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <div className="max-h-[60vh] overflow-y-auto pr-4 pl-1 space-y-4">
              <FormField
                control={form.control}
                name="entityType"
                render={({ field }) => (
                  <FormItem className="space-y-3">
                    <FormLabel className="text-black">Entity Type</FormLabel>
                    <FormControl>
                      <RadioGroup
                        onValueChange={field.onChange}
                        defaultValue={field.value}
                        className="flex space-x-4"
                      >
                        <FormItem className="flex items-center space-x-2 space-y-0">
                          <FormControl>
                            <RadioGroupItem value="Individual" id="individual" />
                          </FormControl>
                          <FormLabel htmlFor="individual" className="font-normal text-black">Individual</FormLabel>
                        </FormItem>
                        <FormItem className="flex items-center space-x-2 space-y-0">
                          <FormControl>
                            <RadioGroupItem value="Non-Individual" id="non-individual"/>
                          </FormControl>
                          <FormLabel htmlFor="non-individual" className="font-normal text-black">Non-Individual</FormLabel>
                        </FormItem>
                      </RadioGroup>
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                 <FormField control={form.control} name="tpTIN" render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-black">Taxpayer Identification Number</FormLabel>
                      <FormControl><Input placeholder="000-000-000" {...field} className={inputStyles} /></FormControl>
                      <FormMessage />
                    </FormItem>
                  )} />
                 <FormField control={form.control} name="branchCode" render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-black">Branch Code</FormLabel>
                      <FormControl><Input placeholder="00000" {...field} className={inputStyles} /></FormControl>
                      <FormMessage />
                    </FormItem>
                  )} />
                 <FormField control={form.control} name="rdoCode" render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-black">RDO Code</FormLabel>
                      <FormControl><Input placeholder="e.g. 038" {...field} className={inputStyles} /></FormControl>
                      <FormMessage />
                    </FormItem>
                  )} />
              </div>

              {entityType === 'Individual' ? (
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <FormField control={form.control} name="firstName" render={({ field }) => (
                    <FormItem><FormLabel className="text-black">First Name</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="middleName" render={({ field }) => (
                    <FormItem><FormLabel className="text-black">Middle Name</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                  )} />
                  <FormField control={form.control} name="lastName" render={({ field }) => (
                    <FormItem><FormLabel className="text-black">Last Name</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                  )} />
                </div>
              ) : (
                <FormField control={form.control} name="companyName" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">Company Name</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                )} />
              )}
              
              <FormField control={form.control} name="tradeName" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">Trade Name (Optional)</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
              )} />
              
              <h3 className="text-lg font-medium pt-4 text-black">Registered Address</h3>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <FormField control={form.control} name="subStreet" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">Unit/Floor/Substreet</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                )} />
                <FormField control={form.control} name="street" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">Street</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                )} />
                <FormField control={form.control} name="barangay" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">Barangay</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                )} />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <FormField control={form.control} name="cityMunicipality" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">City/Municipality</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                )} />
                <FormField control={form.control} name="province" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">Province</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                )} />
                <FormField control={form.control} name="zipCode" render={({ field }) => (
                  <FormItem><FormLabel className="text-black">Zip Code</FormLabel><FormControl><Input {...field} className={inputStyles} /></FormControl><FormMessage /></FormItem>
                )} />
              </div>
              
              <h3 className="text-lg font-medium pt-4 text-black">Taxpayer Type</h3>
               <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <FormField control={form.control} name="cycleType" render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-black">Cycle Type</FormLabel>
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                          <FormControl><SelectTrigger className={inputStyles}><SelectValue placeholder="Select a cycle type" /></SelectTrigger></FormControl>
                          <SelectContent>
                            <SelectItem value="Calendar">Calendar</SelectItem>
                            <SelectItem value="Fiscal">Fiscal</SelectItem>
                          </SelectContent>
                        </Select>
                      <FormMessage />
                    </FormItem>
                  )} />
                  <FormField control={form.control} name="monthSelect" render={({ field }) => (
                    <FormItem>
                      <FormLabel className="text-black">Fiscal Year End Month</FormLabel>
                        <Select onValueChange={field.onChange} defaultValue={field.value} disabled={form.watch('cycleType') === 'Calendar'}>
                          <FormControl><SelectTrigger className={inputStyles}><SelectValue placeholder="Select a month" /></SelectTrigger></FormControl>
                          <SelectContent>
                            {months.map(month => <SelectItem key={month} value={month}>{month}</SelectItem>)}
                          </SelectContent>
                        </Select>
                      <FormMessage />
                    </FormItem>
                  )} />
              </div>

            </div>
            <DialogFooter className="pt-4">
              <DialogClose asChild>
                <Button type="button" variant="outline" className="text-black border-slate-300 hover:bg-slate-100">Cancel</Button>
              </DialogClose>
              <Button type="submit" disabled={isPending}>
                {isPending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Save changes
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
